import achart from '@/components/charts/AChart.vue'

import common from '@/utils/common.js';
import mine from '@/utils/mine.js';
import record from '@/utils/record.js';
import notice from '@/utils/notice.js';
import Bmob from "hydrogen-js-sdk";
let that;
let uid;

export default {
	components: {
		achart
	},
	data() {
		return {
			achartShow: true,
			now_day: common.getDay(0),
			user: "",
			identity: uni.getStorageSync("identity"),
			othercurrent: '',
			noticeText: '', //首页消息提示内容
			logsList: [],

			goodsAnalysis: {
				total_products: 0,
				total_reserve: 0,
				total_money: 0,
				warn_num: 0,
				over_num: 0,
			}, //产品分析的数据
			customsAnalysis: {}, //客户分析的数据
			producersAnalysis: {}, //客户分析的数据
			staffCan: {
				see: false
			} //员工权限
		}
	},
	onLoad(options) {
		that = this;
		uid = uni.getStorageSync('uid');
	},

	onShow() {
		if (uni.getStorageSync("user")) {
			that.loadallGoods();
			that.user = uni.getStorageSync("user")
			that.achartShow = true;
			notice.getNoticeList().then(res => {
				that.noticeText = res
			})

			if (uni.getStorageSync("identity") == 2  &&that.user.rights.othercurrent && that.user.rights.othercurrent.indexOf("2") != -1) {
				that.staffCan.see = true
			}

			mine.getMineInfo().then(res => {

				if (res.disabled) {
					setTimeout(function() {
						uni.showToast({
							title: "该账户已禁用",
							icon: "none"
						})
					}, 1000)

					uni.reLaunch({
						url: "/pages/landing/landing"
					})
				}
			})
		}

		uni.removeStorageSync("now_product")
	},

	onHide() {
		that.achartShow = false
	},

	methods: {
		showNotice() {
			uni.showToast({
				icon: "none",
				title: "非会员无法使用"
			})
		},

		//得到总库存数和总金额
		loadallGoods: function() {
			record.loadallGoods().then(res => {
				that.goodsAnalysis = res
				record.getAllCustom().then(res => {
					that.customsAnalysis = res
					record.getAllProducer().then(res => {
						that.producersAnalysis = res
						/*record.getAllAccounts().then(res=>{
							console.log(res)
						})*/
					})
				})
			})
		},

		//得到日志列表
		get_logsList() {
			const query = Bmob.Query("logs");
			query.equalTo("parent", "==", uid);
			query.equalTo("type", "!=", -2);
			query.order("-createdAt")
			query.limit(20);
			query.find().then(res => {
				//console.log(res)
				for (let item of res) {
					if (item.type == 5) {
						item.link = '/pages/manage/good_det/good_det?id=' + item.detail_id + '&type=false'
					} else if (item.type == -1 || item.type == 1 || item.type == 2 || item.type == 3) {
						item.link = '/pages/report/EnteringHistory/detail/detail?id=' + item.detail_id
					}
				}
				that.logsList = res
			});
		},

	}
}
